@font-face {
    font-family: 'iconfont';
    src: url('./iconfont.eot');
    src: url('./iconfont.eot?#iefix') format('embedded-opentype'),
        url('./iconfont.woff2') format('woff2'),
        url('./iconfont.woff') format('woff'),
        url('./iconfont.ttf') format('truetype'),
        url('./iconfont.svg#iconfont') format('svg');
}

.menu {
    transition: all 1s ease-in-out;
    color: #555;
    width: auto;
    height: 400px;
    width: 100%;
    position: relative;
    top: 0px;
    bottom: 0px;
    background-size: cover;
    background-image: url(https://pg12138.oss-cn-beijing.aliyuncs.com/img/2020/001.jpg);
}

.iconfont {
    font-family: "iconfont" !important;
    font-size: 22px;
    color: #555;
    text-align: center;
    font-style: normal;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

.contain {
    position: absolute;
    top: 0px;
    bottom: 0px;
    right: 0px;
    left: 0px;
    width: 300px;
    height: 200px;
    margin: auto;
}

.point {
    font-family: "iconfont" !important;
    font-style: normal;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;

    cursor: pointer;
    position: absolute;
    font-size: 50px;
    text-align: center;
    line-height: -25px;
    width: 100px;
    height: 100px;
    bottom: 2px;
    right: 0;
    left: 0;
    margin: auto;
}

.title {
    text-align: center;
    font-size: 3rem;
    // margin: -50px 0 30px 0;
}

.link {
    display: flex;
}

.button {
    cursor: pointer;
    width: 70px;
    margin: 0 5px 0 0;
    padding-top: 12px;
    background-color: rgba(33, 150, 243, 0.2);
    border-radius: 8px;
    float: left;
    height: 80px;
}

.button:hover {
    transition: all .4s ease-in-out;
    background-color: #FFF;
}

.button {
    transition: all .4s ease-in-out;

}

.button p {
    width: 29px;
    margin: 0 auto;
    color: rgba(85, 85, 85, 0.85);
}




